}

.flatpickr-days+.flatpickr-time {
    border: 1px solid rgba(72, 72, 72, .1);
    border-top: 0
}

.flatpickr-am-pm {
    outline: 0;
    width: 21%;
    padding: 0 2%;
    cursor: pointer;
    text-align: left;
    font-weight: 300
}

@media all and (-ms-high-contrast:none) {
    .flatpickr-month {
        padding: 0
    }
}
